import React from "react";

export const Calculate = () => {
    function calChmod() {
        var binary = 0;
        var symbol = '';
        //document.getElementById("root").innerHTML = "true";
        //
        if (document.getElementById("ownerRead").checked) { binary += 400; symbol += 'r'; } else { symbol += '-'; }
        if (document.getElementById("ownerWrite").checked) { binary += 200; symbol += 'w'; } else { symbol += '-'; }
        if (document.getElementById("ownerExecute").checked) { binary += 100; symbol += 'x'; } else { symbol += '-'; }

        if (document.getElementById("groupRead").checked) { binary += 40; symbol += 'r'; } else { symbol += '-'; }
        if (document.getElementById("groupWrite").checked) { binary += 20; symbol += 'w'; } else { symbol += '-'; }
        if (document.getElementById("groupExecute").checked) { binary += 10; symbol += 'x'; } else { symbol += '-'; }

        if (document.getElementById("otherRead").checked) { binary += 4; symbol += 'r'; } else { symbol += '-'; }
        if (document.getElementById("otherWrite").checked) { binary += 2; symbol += 'w'; } else { symbol += '-'; }
        if (document.getElementById("otherExecute").checked) { binary += 1; symbol += 'x'; } else { symbol += '-'; }

        document.getElementById("binaryVal").innerHTML = binary;
        document.getElementById("symbolVal").innerHTML = symbol;
       
        binary = 0;
        symbol = '';
    }
    return (
        <div>
            <table class="container">
                <tr>
                    <th>Permissions</th>
                    <th>Read (r)</th>
                    <th>Write (w)</th>
                    <th>Execute (x)</th>
                    <th>Chmod value</th>
                </tr>
                <tr>
                    <th>Owner</th>
                    <td><input type="checkbox" id="ownerRead" onClick={calChmod}></input></td>
                    <td><input type="checkbox" id="ownerWrite" onClick={calChmod}></input></td>
                    <td><input type="checkbox" id="ownerExecute" onClick={calChmod}></input></td>
                    <td rowSpan="3" id="highlight"><p id="root">chmod <span id="binaryVal"></span><br></br><span id="symbolVal"></span></p></td>
                </tr>
                <tr>
                    <th>Group</th>
                    <td><input type="checkbox" id="groupRead" onClick={calChmod}></input></td>
                    <td><input type="checkbox" id="groupWrite" onClick={calChmod}></input></td>
                    <td><input type="checkbox" id="groupExecute" onClick={calChmod}></input></td>
                </tr>
                <tr>
                    <th>Other</th>
                    <td><input type="checkbox" id="otherRead" onClick={calChmod}></input></td>
                    <td><input type="checkbox" id="otherWrite" onClick={calChmod}></input></td>
                    <td><input type="checkbox" id="otherExecute" onClick={calChmod}></input></td>
                </tr>
            </table>
        </div>
            
    )
}
